html {
  height: 100%;
}
body {
  height: 100%;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 26pt;
  display: flex;
  background: #f6f6f6;
}
.content {
  height: 100%;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: space-around;
  z-index: 20;
  position: relative;
  width: 996px;
}
.content h1 {
  padding: 0;
  margin: 0;
  margin-top: 20px;
  font-size: 32px;
  line-height: 20pt;
  font-weight: 500;
  margin-bottom: 3em;
}
.content [class^='form__1-'] {
  border: 1px solid #dadef0;
  border-radius: 4px;
  height: 56px;
  box-sizing: border-box;
  padding: 1em 1em;
  position: relative;
}
.content [class^='form__1-'] + span {
  position: relative;
}
.content [class^='form__1-'] + span::before {
  content: "Заполните поле верно";
  position: absolute;
  font-size: 10px;
  top: -7px;
  color: #ff1e38;
  display: none;
}
.content [class^='form__1-']:hover {
  cursor: pointer;
  border: 1px solid #c3c9db;
  outline: none;
}
.content [class^='form__1-']:focus {
  border-color: #8288a2;
  outline: none;
}
.content [class^='form__1-']:disabled {
  color: #8288a2;
  background: #edeeef;
  outline: none;
}
.content [class^='form__1-']::placeholder {
  outline: none;
  color: #b8bed8;
}
.content [class^='form__1-']:valid {
  border-color: #dadef0;
  outline: none;
}
.content [class^='form__1-']:not(:placeholder-shown):invalid {
  border-color: #ff1e38;
  box-shadow: none;
  outline: none;
}
.content [class^='form__1-']:not(:placeholder-shown):invalid ~ span::before {
  display: block;
}
.content .invalidValue {
  border-color: #ff1e38;
  box-shadow: none;
}
.content .invalidValue ~ span::before {
  display: block;
}
.content .invalidValue:hover {
  border-color: red;
  box-shadow: none;
}
.content .form {
  background: white;
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  height: auto;
  max-width: 996px;
  min-width: 250px;
  z-index: 40;
}
.content .form__adaptive {
  display: none;
}
.content .form__1 {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  justify-content: space-around;
  width: 100%;
  height: 530px;
  padding: 2% 3%;
}
.content .form__1 label {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  font-size: 14px;
}
.content .form__1 .alignment {
  display: flex;
  justify-content: space-between;
  max-width: 932px;
  min-width: 250px;
}
.content .form__1 .alignment label {
  width: 49.15%;
}
.content .form__1-address {
  max-width: 932px;
  min-width: 250px;
}
.content .form__1-comment {
  resize: none;
  max-width: 932px;
  height: 8em;
  min-width: 250px;
}
.content .form__2 {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  width: 100%;
  height: 767px;
  padding: 2% 3%;
}
.content .form__2 div:nth-child(1) {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  max-width: 932px;
  min-width: 250px;
}
.content .form__2 div:nth-child(1) label {
  width: 44%;
  margin-left: 25px;
  font-size: 14px;
  position: relative;
}
.content .form__2 div:nth-child(1) label span {
  position: relative;
}
.content .form__2 div:nth-child(1) label .radio1::after,
.content .form__2 div:nth-child(1) label .radio2::after {
  content: "";
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #212bff;
  left: 0;
  left: -20px;
  top: 5px;
  display: none;
}
.content .form__2 div:nth-child(1) label .radio1::before,
.content .form__2 div:nth-child(1) label .radio2::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border: 1px solid #dadef0;
  border-radius: 50%;
  top: -3px;
  left: -28px;
}
.content .form__2 div:nth-child(1) label:hover .radio1::before,
.content .form__2 div:nth-child(1) label:hover .radio2::before {
  border: 1px solid #8288a2;
}
.content .form__2 div:nth-child(1) label input {
  display: none;
  margin-right: 10px;
}
.content .form__2 div:nth-child(1) label input:disabled ~ .radio1::before,
.content .form__2 div:nth-child(1) label input:disabled ~ .radio2::before {
  background: #edeeef;
  border-color: #edeeef;
}
.content .form__2 div:nth-child(1) label input:disabled ~ span {
  color: #8288a2;
}
.content .form__2 div:nth-child(1) label input:checked ~ .radio1::after,
.content .form__2 div:nth-child(1) label input:checked ~ .radio2::after {
  display: block;
}
.content .form__2 div:nth-child(1) label input:checked ~ .radio1::before,
.content .form__2 div:nth-child(1) label input:checked ~ .radio2::before {
  border-color: #212bff;
}
.content .form__2 div:nth-child(2) {
  max-width: 932px;
  min-width: 250px;
  height: 582px;
  margin-top: 1em;
}
.notAdaptive[name^='tab'] {
  display: none;
}
.notAdaptive[name^='tab'][id='delivery']:checked ~ label span.tab1::before,
.notAdaptive[name^='tab'][id='delivery']:checked ~ label span.tab1::after {
  z-index: -1;
  background: white;
}
.notAdaptive[name^='tab'][id='delivery']:checked ~ label span.tab1 {
  color: #212bff;
}
.notAdaptive[name^='tab'][id='delivery']:not(:checked) ~ .form__1 {
  display: none;
}
.notAdaptive[name^='tab'][id='pickup']:checked ~ label span.tab2::before,
.notAdaptive[name^='tab'][id='pickup']:checked ~ label span.tab2::after {
  z-index: -1;
  background: white;
}
.notAdaptive[name^='tab'][id='pickup']:checked ~ label span.tab2 {
  color: #212bff;
}
.notAdaptive[name^='tab'][id='pickup']:not(:checked) ~ .form__2 {
  display: none;
}
.notAdaptive + label {
  position: absolute;
  top: -48px;
  left: 0;
  border-radius: 4px 4px 0px 4px;
  height: 60px;
  width: 120px;
}
.notAdaptive + label span {
  position: relative;
  color: #8288a2;
  font-size: 14px;
  left: 35px;
  user-select: none;
}
.notAdaptive + label span:hover {
  color: #212bff;
}
.notAdaptive + label span.tab1::before,
.notAdaptive + label span.tab2::before {
  content: "";
  position: absolute;
  height: 65px;
  width: 100px;
  background: #edeeef;
  border-radius: 4px 0px 4px 0px;
  left: -35px;
  top: -25px;
  z-index: -2;
}
.notAdaptive + label span.tab2::after,
.notAdaptive + label span.tab1::after {
  content: "";
  position: absolute;
  height: 65px;
  width: 80px;
  border-radius: 0px 4px 0px 0px;
  background: #edeeef;
  transform: skew(30deg);
  left: 35px;
  top: -25px;
  z-index: -2;
  box-shadow: 2px 0px 0px #dadef0;
}
.notAdaptive + label[for^="pickup"] {
  left: 135px;
}
button {
  margin-top: 1em;
  margin-left: auto;
  width: 208px;
  height: 56px;
  background: none;
  border: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  line-height: 24pt;
  font-size: 16px;
  border-radius: 28px;
  background-color: #212bff;
  color: white;
  transition: all 0.2s;
}
button:hover {
  background-color: #2029ea;
}
button:active {
  background: #1e26d8;
}
button:disabled {
  color: #8288a2;
  background: #edeeef;
}
.button-margin {
  margin-left: auto;
}
.button-margin--button2 {
  margin-top: 2em;
}

@media (min-width: 1200px) {
  .content {
    margin: auto ;
  }
}
@media screen {
  [class^='form__1-']:hover {
    cursor: pointer;
    border: 1px solid #c3c9db;
  }
  [class^='form__1-']:focus {
    border-color: #8288a2;
  }
  [class^='form__1-']:disabled {
    color: #8288a2;
    background: #edeeef;
  }
  [class^='form__1-']::placeholder {
    color: #b8bed8;
  }
  [class^='form__1-']:valid {
    border-color: #dadef0;
  }
}
@media (max-width: 768px) {
  .content h1 {
    font-size: 20pt;
  }
  input[name='tab'] + label {
    display: none;
  }
  .content .form__1,
  .content .form__2 {
    display: none;
  }
  .content .form {
    padding: 0;
  }
  .content .form__adaptive ~ label {
    position: relative;
    display: flex;
    border-top: 1px solid #dadef0;
    border-bottom: 1px solid #dadef0;
    padding: 1em 2em;
    padding: 0.6em 1.2em;
    color: #8288a2;
    max-width: 100%;
    min-wdith: 280px;
  }
  .content .form__adaptive ~ label:hover {
    cursor: pointer;
    color: #212bff;
  }
  .content .form__adaptive ~ label:hover + label::after {
    color: #212bff;
  }
  .content .form__adaptive ~ label::after {
    content: "";
    position: absolute;
    width: 7px;
    height: 7px;
    border-left: 3px solid #8288a2;
    border-bottom: 3px solid #8288a2;
    transform: rotate(-45deg);
    right: 3%;
    top: 20px;
  }
  .content .form__adaptive[id=delivery1]:checked ~ label[for=delivery1] {
    color: #212bff;
  }
  .content .form__adaptive[id=delivery1]:checked ~ label[for=delivery1]::after {
    transform: rotate(135deg);
    border-color: #212bff;
  }
  .content .form__adaptive[id=delivery1]:checked ~ .form__1 {
    display: flex;
  }
  .content .form__adaptive[id=pickup2]:checked ~ label[for=pickup2] {
    color: #212bff;
  }
  .content .form__adaptive[id=pickup2]:checked ~ label[for=pickup2]::after {
    transform: rotate(135deg);
    border-color: #212bff;
  }
  .content .form__adaptive[id=pickup2]:checked ~ .form__2 {
    display: flex;
  }
  .content .form__1 {
    height: 700px;
  }
  .content .form .alignment {
    flex-direction: column;
    height: 200px;
  }
  .content .form .alignment label {
    display: flex;
    width: 100%;
  }
  .content .form .alignment label input {
    width: 100%;
  }
  .content .form__2 div:nth-child(1) {
    display: flex;
    flex-direction: column;
  }
  .content .form__2 div:nth-child(1) label {
    display: flex;
    width: 100%;
  }
}
